import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { closeModal } from '../../utils/handleModal';

/**
 * Modal with custom text and optional links and buttons
 * @component
 * @param { {text: string, links: Array<{path: string, text: string}>, buttons: Array<{action: Function, text: string}> } } Props
 * @returns
 */
function Modal({ text, links = false, buttons = false }) {
  return (
    <dialog className='modal' data-testid='dialog'>
      <button
        className='close'
        onClick={() => closeModal()}
        data-testid='close-button'
      >
        <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'>
          <path d='M0 256C0 114.6 114.6 0 256 0C397.4 0 512 114.6 512 256C512 397.4 397.4 512 256 512C114.6 512 0 397.4 0 256zM175 208.1L222.1 255.1L175 303C165.7 312.4 165.7 327.6 175 336.1C184.4 346.3 199.6 346.3 208.1 336.1L255.1 289.9L303 336.1C312.4 346.3 327.6 346.3 336.1 336.1C346.3 327.6 346.3 312.4 336.1 303L289.9 255.1L336.1 208.1C346.3 199.6 346.3 184.4 336.1 175C327.6 165.7 312.4 165.7 303 175L255.1 222.1L208.1 175C199.6 165.7 184.4 165.7 175 175C165.7 184.4 165.7 199.6 175 208.1V208.1z' />
        </svg>
      </button>
      <h3>{text}</h3>
      <div className='actions-container'>
        {links &&
          links.map(
            (link, index) =>
              link.path &&
              link.text && (
                <Link key={`modal-link-${index}`} to={link.path}>
                  {link.text}
                </Link>
              )
          )}
        {buttons &&
          buttons.map(
            (button, index) =>
              button.action &&
              button.text && (
                <button
                  key={`modal-button-${index}`}
                  onClick={() => button.action()}
                >
                  {button.text}
                </button>
              )
          )}
      </div>
    </dialog>
  );
}

export default Modal;

Modal.propTypes = {
  text: PropTypes.string,
  links: PropTypes.arrayOf(
    PropTypes.shape({
      path: PropTypes.string,
      text: PropTypes.string,
    })
  ),
  buttons: PropTypes.arrayOf(
    PropTypes.shape({
      action: PropTypes.func,
      text: PropTypes.string,
    })
  ),
};
